.triple-spinner {
  display: inline-block;
  position: relative;
  margin: 1em 0;
  width: 7em;
  height: 7em;
  border-radius: 50%;
  border: 4px solid transparent;
  border-top: 4px solid #FF5722;
  -webkit-animation: spin 2s linear infinite;
  animation: spin 2s linear infinite;
}

.triple-spinner::before,
.triple-spinner::after {
  content: "";
  position: absolute;
  border-radius: 50%;
  border: 4px solid transparent;
}
.triple-spinner::before {
  top: 5px;
  left: 5px;
  right: 5px;
  bottom: 5px;
  border-top-color: #FF9800;
  -webkit-animation: spin 3s linear infinite;
  animation: spin 3.5s linear infinite;
}
.triple-spinner::after {
  top: 15px;
  left: 15px;
  right: 15px;
  bottom: 15px;
  border-top-color: #FFC107;
  -webkit-animation: spin 1.5s linear infinite;
  animation: spin 1.75s linear infinite;
}
@-webkit-keyframes spin {
    from {
      -webkit-transform: rotate(0deg);
      transform: rotate(0deg);
    }
    to {
      -webkit-transform: rotate(360deg);
      transform: rotate(360deg);
    }
  }
  

@keyframes spin {
    from {
      -webkit-transform: rotate(0deg);
      transform: rotate(0deg);
    }
    to {
      -webkit-transform: rotate(360deg);
      transform: rotate(360deg);
    }
  }

.detail{
  display: grid;
  grid-template-columns: 1fr 2fr;
  grid-template-rows: 20vh 50vh;
  grid-column-gap: 2.5em;
  min-height: 100vh;
  -webkit-align-items: center;
          align-items: center;
  color: white;
  font-size: 20px;
}

.button {
  grid-column: 1 / -1;
}

.sprite {
  height: 13em;
  justify-self: center;
}

.basic-details {
  line-height: 1;
  margin-bottom: 0.5em;
}

.id {
  font-size: 1.5em;
}

.icon {
  width: .8em;
  height: .8em;
  margin-right: 5px;
}

.name {
  font-size: 3em;
}

.types {
  color: 'black';
  font-weight: 'bold';
  padding: '3px 10px 5px 10px';
}

.specific-details {
  margin-top: 1em;
  padding: 30px;
  border: solid 1px black;
  border-radius: 15px;
  background-color: #3C3C3C;
  color: #eaeaea;
  font-size: 0.9em;
}

.description {
  line-height: 1.5;
}

.triple-spinner.detail-loading {
  position: fixed;
  top: 50%;
  left: 50%;
  margin-top: -50px;
  margin-left: -100px;
}
body {
